<template>
  <div class="comment">
    <h3 class="comm-title"></h3>
    <hr>
    <textarea name="commConent" placeholder="bb120" v-model="msg"></textarea>
    <mt-button type="primary" size="large" @click="postComment"></mt-button>
    <div class="comm-area" v-for="(item,i) in comments" :key="item.add_time">
      <p
        class="comm-header"
      >{{ i+1 }}&nbsp;&nbsp;&nbsp;:{{ item.user_name }}&nbsp;&nbsp;:{{item.add_time|dataTime}}</p>
      <p class="comm-body">{{item.content==="undefined"?"":item.content}}</p>
    </div>
    <mt-button size="large" type="danger" plain @click="getMore"></mt-button>
  </div>
</template>
<script>
import { Toast } from "mint-ui"

export default {
  data() {
    return {
      pageindex: 1,
      comments: [],
      msg: ""
    };
  },
  created() {
    this.getComments()
  },
  methods: {
    getComments() {
      this.$http
        .get("api/getcomments/" + this.id + "?pageindex=" + this.pageindex)
        .then(res => {
          if (res.body.status === 0) {
            this.comments = this.comments.concat(res.body.message);
          } else {
            Toast("");
          }
        });
    },
    getMore() {
      this.pageindex++
      this.getComments()
    },
    postComment() {
      if (this.msg.trim().length === 0) {
        return Toast("bb");
      }
      this.$http
        .post("api/postcomment/" + this.$route.params.id, {
          content: this.msg.trim()
        })
        .then(res => {
          if (res.body.status === 0) {
            // 
            let newComm = {
              user_name: "",
              add_time: Date.now(),
              content: this.msg.trim()
            }
            this.comments.unshift(newComm)
            // this.pageindex=1
            // this.getComments()
            this.msg=''
          } else {
            Toast("")
            this.msg=''
          }
        });
    }
  },
  props: ["id"]
};
</script>
<style lang="less" scoped>
.comment {
  padding-bottom: 10px;
  .comm-title {
    font-size: 20px;
  }
  textarea {
    height: 88px;
    margin: 0;
    font-size: 14px;
  }
  .comm-area {
    .comm-header {
      height: 30px;
      line-height: 30px;
      background-color: #aaa;
      margin-top: 6px;
      font-size: 12px;
      color: #111;
      padding-left: 5px;
    }
    .comm-body {
      font-size: 14px;
      padding-left: 20px;
      color: #222;
    }
  }
}
</style>
